.searchContainer {
  flex-direction: column;
  display: flex;
  gap: 10px;
  padding: 10px;
}

.searchResultsContainer {
  margin-top: 10px; /* sau orice spațiere dorești între formular și rezultate */
}

.Header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  background-color: #2cbb7f;
  margin-bottom: 5px;
}

.Searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #2cbb7f;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #2cbb7f;
  border-radius: 3px;
  overflow: hidden;
}

.SearchFormButton {
  margin-right: 5px;
  display: inline-block;
  width: 48px;
  height: 48px;
  border-radius: 10px;
  border: 1px solid #2cbb7f;
  background-image: url('/src/assets/images/search.svg');
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.SearchFormButton:hover,
.SearchFormButton:focus {
  background-color: transparent;
  border: 1px solid #212121;
}

.SearchFormButtonLabel {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.SearchFormInput {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding: 4px;
  border-radius: 10px;
}

.SearchFormInput::placeholder {
  font: inherit;
  font-size: 18px;
}

.list {
  list-style-type: none;
}

.navLink {
  color: #0e0e0e;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 1.25px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  text-decoration: none;
}

.navLink a:hover,
.navLink a:focus {
  text-decoration: underline;
}

.navLinkActive {
  color: #258b25 !important;
}
